.MatchPage {
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-column-gap: 20px; /* increase column gap */

    .year-selector {
        justify-self: end;
        align-self: start;
        padding: 20px;
       
    }

    .page-heading {
        margin: 20px auto;
    }
}